import uniPopup from '../../components/uni-popup/uni-popup.vue'
export default {
	onLoad() {
		this.getStorage()
	},
	data() {
		return {
			title: 'map',
			latitude: 39.909,
			longitude: 116.39742,
			isSelectedId: 0,
			canelInfo: [{
				info: '信息填写错误'
			}, {
				info: '重复下单'
			}, {
				info: '时间来不及'
			}, {
				info: '不想修了'
			}, {
				info: '其他原因'
			}],
			orderNum: '1545545489646',
			orderTime: '2019.08.12 12:34:32',
			type: '上门维修',
			onTime: '8月12日 15：30',
			address: '郫都区望丛中路13号7栋203号',
			totalPrice: '245',
			repaireList: [{
				title: '屏幕损坏',
				price: '499'
			}, {
				title: '电池不续航',
				price: '599'
			}],
			brand: '',
			model: '',
			colourName: ''
		}
	},
	methods: {
		openPopup() {
			this.$refs.popup.open()
		},
		closePopup() {
			this.$refs.popup.close()
		},
		onCancel() {
			uni.showToast({
				title: '取消成功'
			})
			setTimeout(() => {
				uni.reLaunch({
					url: 'order-cancel'
				})
			}, 1000)
		},
		onSelectedId(e) {
			this.isSelectedId = e.currentTarget.id
		},
		bindIndex() {
			uni.reLaunch({
				url: '../index/index'
			})
		},
		getStorage() {
			uni.getStorage({
				key: 'brand',
				success: res => {
					console.log(res.data)
					this.brand = res.data
				}
			})
			uni.getStorage({
				key: 'model',
				success:res=>{
					this.model = res.data
				}
			})
			uni.getStorage({
				key: 'colourName',
				success:res=>{
					this.colourName = res.data
				}
			})
			uni.getStorage({
				key: 'colourName',
				success:res=>{
					this.colourName = res.data
				}
			})
		}
	},
	components: {
		uniPopup
	}

}
